header {
    background-color: #40474d;
    color: #4ab3b7;
    font-family: "Trajan Pro", Courier, monospace;
    padding: 20px;
    width: 100%;
    text-align: center;
    font-size: 50px;
  }
  
  footer {
    background-color: #40474d;
    color: #4ab3b7;
    font-family: "Trajan Pro", Courier, monospace;
    padding: 20px;
    width: 100%;
    text-align: center;
    font-size: 24px;
  }
  
  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Arial, sans-serif;
    text-align: center;
    padding-top: 25px;
    padding-left: 10px;
    width: 100%;
    height: 100%;
  }
  
  .scroll {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  body {
    margin: 0;
    height: 100%;
    background-color: #dbcda7;
  }
  
  .scrollButton {
    height: 2rem;
    width: 4rem;
    font-size: 25px;
    background-color: transparent;
    margin: 1rem;
    display: flex;
    align-items: center;
  }
  
  .movieList {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 1rem;
  }
  
  h1 {
    color: black;
  }
  
  h2 {
    color: black;
    font-size: 24px;
  }
  
  .movieList > div {
    color: white;
  }
  
  button {
    background-color: #000;
    font-size: 16px;
    color: #0b76f5;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin: 10px;
  }
  
  .movie {
    cursor: pointer;
  }
  
  .clickedContent {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    margin: 0 auto;
    max-width: 80%;
    max-height: 80%;
    display: flex;
    flex-direction: column;
  }
  
  .movieInfo {
    padding: 10px;
    font-size: 22px;
    text-align: left;
    margin-bottom: 10px;
  }
  
  .closeButton {
    cursor: pointer;
    font-size: 16px;
    background-color: white;
    border: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
    margin-top: 20px;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  }
  